<template><section><h1>WallGeometry, WallOutlineGeometry</h1>
<ul>
<li>The <code v-pre>vc-geometry-wall</code> component is used to load a geometry that describes a wall, which is similar to a KML line string. A wall is defined by a series of points, which extrude down to the ground. Optionally, they can extrude downwards to a specified height.</li>
<li>The <code v-pre>vc-geometry-outline-wall</code> component is used to load a geometry that describes a wall outline. A wall is defined by a series of points, which extrude down to the ground. Optionally, they can extrude downwards to a specified height.</li>
<li>It needs to be used as a sub-component of <code v-pre>vc-instance-geometry</code>, can be mounted on <code v-pre>vc-primitive</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load WallGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-wall ref="wall" :positions="positions" :vertexFormat="vertexFormat"></vc-geometry-wall>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-wall ref="wallOutline" :positions="positionsOutline"></vc-geometry-outline-wall>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
