<h2>Instance Properties</h2>
<h3><code v-pre>vc-geometry-wall</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>required</code> An array of Cartesian objects, which are the points of the wall. <strong>structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The distance, in radians, between each latitude and longitude. Determines the number of positions in the buffer.</td>
</tr>
<tr>
<td>maximumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> An array parallel to positions that give the maximum height of the wall at positions. If undefined, the height of each position in used.</td>
</tr>
<tr>
<td>minimumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code>An array parallel to positions that give the minimum height of the wall at positions. If undefined, the height at each position is 0.0.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid for coordinate manipulation.</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The vertex attributes to be computed.</td>
</tr>
</tbody>
</table>
<hr>
